/**
 * Game
 */

.game__wrapper {
    position: relative;
    padding: 25px;
    background: $dark;

    .game__scene {
        position: relative;
        display: block;
        height: calc(100vh - 50px);
        background: $light;
        overflow: hidden;

        #game__play {
            display: block;
            width: 100%;
            height: 100%;
        }
    }

    .exit, .play {
        position: absolute;
        top: 0;
        left: 0;
        padding: 20px;
        border-bottom: 1px solid;
        border-left: 1px solid;
        font-size: 18px;
        font-weight: 700;
        color: white;
        text-transform: uppercase;
    }

    .exit {
        border-color: brown;
        background: brown;
    }

    .play {
        border-color: green;
        background: green;
    }
    
    .score {
        display: inline-block;
        padding: 20px;
        border-bottom: 1px solid black;
        border-left: 1px solid black;
        font-size: 18px;
        font-weight: 700;
        color: black;
        text-transform: uppercase;

        .item {
            padding-right: 20px;
        }
    }
}
