.coins {
    position: absolute;
    top: 0;
    left: 0;
    padding: 10px;
    .item {
        display: inline-block;
        min-width: 35px;

        .coin__label {
            display: none;
        }

        .coin__flip {
            position: relative;
            width: 35px;
            top: 0;
            right: 0;
            z-index: 5;
            animation: coin__rotation 2s infinite;
        }

        @keyframes coin__rotation {
            0% {
                transform: rotateY(0);
            }
            50% {
                transform: rotateY(-180deg);
                margin-left: -2px;
            }
            100% {
                transform: rotateY(-360deg);
            }
        }
    }

    .value {
        padding-left: 5px;
        font-size: 18px;
        line-height: 30px;
    }
}
