.game__dashboard__wrapper {
    display: flex;
    flex-flow: column;
    justify-content: space-around;
    height: 100%;

    .game__dashboard {
        display: flex;
        flex-flow: row wrap;
        justify-content: space-around;
    }

    .game__logo {
        flex: 0 0 100%;
        text-align: center;
    }

    .game__navigator {
        margin: 20px 0;
        text-align: center;
    }
}
