.game__button {
    a {
        display: inline-block;
        margin: 10px 0;
        padding: 12.5px 25px;
        border: 1px solid $primary;
        border-radius: 4px;

        background-color: $primary;
        color: $dark;

        font-size: 18px;
        text-transform: uppercase;
        letter-spacing: 1.5px;

        text-decoration: none;
        outline: 0;
    }
}
